//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
	//import引入的组件需要注入到对象中才能使用
	components: {},
	data() {
		return {
			categoryList: [],
			index1: 0,
			index2:0,
			goodDesc: [],
			id:'',
			subList:[]
		};
	},
	//监听属性 类似于data概念
	computed: {},
	//监控data中的数据变化
	watch: {},
	//方法集合
	methods: {
		search(e){
			let keyword=e.detail.value
			uni.navigateTo({
				url:`../search/search?keyword=${keyword}`
			})
		},
		lineControl(item, index) {
			this.index1 = index
			this.id =item.id
			console.log(item)
			uni.request({
				url: this.$api('category/indexaction'),
				method: 'GET',
				data: {},
				success: res => {
					this.goodDesc = res.data.categoryList[this.index1]
					// console.log(this.goodDesc)
				},
				fail: () => {},
				complete: () => {}
			});
			uni.request({
				url: this.$api(`category/currentaction?id=${this.id}`),
				method: 'GET',
				data: {},
				success: res => {
					// console.log(res)
					this.subList =res.data.data.currentOne.subList
					console.log(this.subList)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		goTogoodlist(item,index){
			let indexs =index
			let cateId =this.id
			let goodId =item.id
				
			wx.navigateTo({
				url:`../goodlist/goodlist?cateId=${cateId}&index=${indexs}&goodId=${goodId}`
			})
		}
	},
	//生命周期 - 创建完成（可以访问当前this实例）
	created() {

	},
	//生命周期 - 挂载完成（可以访问DOM元素）
	mounted() {
		uni.request({
			url: this.$api('category/indexaction'),
			method: 'GET',
			data: {},
			success: res => {
				// console.log(res)
				this.categoryList = res.data.categoryList
				this.goodDesc = res.data.categoryList[this.index1]
				console.log(this.goodDesc)
				this.id = res.data.categoryList[this.index1].id
				console.log(this.id)
				// console.log(this.goodDesc)
				uni.request({
					url: this.$api(`category/currentaction?id=${this.id}`),
					method: 'GET',
					data: {},
					success: res => {
						// console.log(res)
						this.subList =res.data.data.currentOne.subList
						console.log(this.subList)
					},
					fail: () => {},
					complete: () => {}
				});
			},
			fail: () => {},
			complete: () => {}
		});
	
	},
	beforeCreate() {}, //生命周期 - 创建之前
	beforeMount() {}, //生命周期 - 挂载之前
	beforeUpdate() {}, //生命周期 - 更新之前
	updated() {}, //生命周期 - 更新之后
	beforeDestroy() {}, //生命周期 - 销毁之前
	destroyed() {}, //生命周期 - 销毁完成
	activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
